.tasks {
  list-style: none;
  margin: 0;
  padding: 0;
}

.tasks__item {
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-gap: 10px;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #3c3b3b;
}

.tasks__item--hidden {
  display: none;
}

.tasks__content--done {
  text-decoration: line-through;
}

.tasks__button {
  color: #ffffff;
  border: none;
  width: 30px;
  height: 30px;
  padding: 0;
  transition: background-color 0.3s;
  cursor: pointer;
}

.tasks__button--toggleDone {
  background-color: hsl(120, 61%, 34%);
}

.tasks__button--toggleDone:hover {
  background-color: hsl(120, 61%, 39%);
}

.tasks__button--toggleDone:active {
  background-color: hsl(120, 61%, 44%);
}

.tasks__button--remove {
  background-color: hsl(348, 61%, 44%);
}

.tasks__button--remove:hover {
  background-color: hsl(348, 61%, 52%);
}

.tasks__button--remove:active {
  background-color: hsl(348, 61%, 57%);
}
